.chart-container {
    height: 100%;
}

.chart-contents-container {
    display: flex;
    list-style-type: none;
    column-gap: 8px;
    padding-left: 0px;
    padding: 20px;

}

.chart-contents {
    cursor: pointer;
    height: 600px;
    display: flex;
    align-items: flex-start;
}

.chart-elements-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    row-gap: 10px;
}

.img-container {
    border-radius: 50%;
    width: 80px;
    height: 80px;
    background: black;
}

.bar {
    border-radius: 4px;
    width: 80px;
    background: red;
}

.chart-index-container {
    display: flex;
    list-style-type: none;
    column-gap: 8px;
    padding-left: 0px;
    height: fit-content;
    padding: 20px;
}

.chart-index {
    text-align: center;
    width: 80px;
}